import React, { useState, useRef, useEffect } from 'react';
import { Send, Home, User, Search, Copy, ThumbsUp, ThumbsDown, Share, RotateCcw, MoreHorizontal } from 'lucide-react';
import { motion, AnimatePresence } from 'framer-motion';
import ReactMarkdown from 'react-markdown';
import CodeBlock from './CodeBlock';
import './NewChatInterface.css';

interface Message {
  id: string;
  text: string;
  sender: 'user' | 'ai';
  timestamp: Date;
}

const NewChatInterface: React.FC = () => {
  const [messages, setMessages] = useState<Message[]>([]);
  const [inputText, setInputText] = useState('');
  const [isTyping, setIsTyping] = useState(false);
  const [activeNav, setActiveNav] = useState('home');
  const [conversationId, setConversationId] = useState<string | null>(null);
  const messagesEndRef = useRef<HTMLDivElement>(null);
  const textareaRef = useRef<HTMLTextAreaElement>(null);

  const scrollToBottom = () => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  };

  useEffect(() => {
    scrollToBottom();
  }, [messages]);

  // Auto-resize textarea
  const handleInputChange = (e: React.ChangeEvent<HTMLTextAreaElement>) => {
    setInputText(e.target.value);
    
    // Auto-resize textarea - reset to auto first, then set to scrollHeight
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
      const scrollHeight = textareaRef.current.scrollHeight;
      textareaRef.current.style.height = `${Math.min(scrollHeight, 200)}px`;
    }
  };

  const handleSendMessage = async () => {
    if (!inputText.trim()) return;

    const userMessage: Message = {
      id: Date.now().toString(),
      text: inputText,
      sender: 'user',
      timestamp: new Date()
    };

    setMessages(prev => [...prev, userMessage]);
    setInputText('');
    
    // Reset textarea height when message is sent
    if (textareaRef.current) {
      textareaRef.current.style.height = 'auto';
    }
    
    setIsTyping(true);

    // Call real backend API
    try {
      const response = await fetch('http://localhost:5000/api/chatOpenAI', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          message: inputText,
          conversation_id: conversationId || undefined
        })
      });

      const data = await response.json();

      if (data.success) {
        // Save conversation ID for future messages
        if (!conversationId) {
          setConversationId(data.conversation_id);
        }

        const aiMessage: Message = {
          id: (Date.now() + 1).toString(),
          text: data.message,
          sender: 'ai',
          timestamp: new Date()
        };

        setMessages(prev => [...prev, aiMessage]);
      } else {
        throw new Error(data.error || 'Failed to get response');
      }
    } catch (error) {
      console.error('Error calling backend:', error);
      const errorMessage: Message = {
        id: (Date.now() + 1).toString(),
        text: "Sorry, I'm having trouble connecting right now. Please try again!",
        sender: 'ai',
        timestamp: new Date()
      };
      setMessages(prev => [...prev, errorMessage]);
    } finally {
      setIsTyping(false);
    }
  };

  const handleKeyPress = (e: React.KeyboardEvent) => {
    if (e.key === 'Enter' && !e.shiftKey) {
      e.preventDefault();
      handleSendMessage();
    }
  };

  return (
    <div className="new-chat-container">
      {/* Liquid Glass Navigation */}
      <motion.div 
        className="liquid-nav"
        initial={{ opacity: 0, y: -20 }}
        animate={{ opacity: 1, y: 0 }}
        transition={{ duration: 0.6, delay: 0.2 }}
      >
        <motion.button
          className={`nav-circle ${activeNav === 'home' ? 'active' : ''}`}
          onClick={() => setActiveNav('home')}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.95 }}
        >
          <Home size={20} />
        </motion.button>
        <motion.button
          className={`nav-circle ${activeNav === 'discover' ? 'active' : ''}`}
          onClick={() => setActiveNav('discover')}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.95 }}
        >
          <Search size={20} />
        </motion.button>
        <motion.button
          className={`nav-circle ${activeNav === 'profile' ? 'active' : ''}`}
          onClick={() => setActiveNav('profile')}
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.95 }}
        >
          <User size={20} />
        </motion.button>
      </motion.div>

      {/* Main Chat Area */}
      <div className="chat-main">
        {/* Welcome Header */}
        {messages.length === 0 && (
          <motion.div 
            className="welcome-section"
            initial={{ opacity: 0, y: 30 }}
            animate={{ opacity: 1, y: 0 }}
            transition={{ duration: 0.8, delay: 0.4 }}
          >
            <h1 className="welcome-title">Hi, Archita</h1>
            <p className="welcome-subtitle">What would you like to explore today?</p>
            
          </motion.div>
        )}

        {/* Messages */}
        <div className="messages-area">
          <AnimatePresence>
            {messages.map((message) => (
              <motion.div
                key={message.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                exit={{ opacity: 0, y: -20 }}
                className={`message ${message.sender}`}
              >
                {message.sender === 'user' ? (
                  <div className="message-bubble">
                    {message.text}
                  </div>
                ) : (
                  <div className="ai-message-content">
                    <div className="ai-message-text">
                      <ReactMarkdown
                        components={{
                          code: ({ className, children, ...props }: any) => {
                            const match = /language-(\w+)/.exec(className || '');
                            const isInline = !className;
                            return !isInline && match ? (
                              <CodeBlock className={className}>
                                {String(children).replace(/\n$/, '')}
                              </CodeBlock>
                            ) : (
                              <code className={className} {...props}>
                                {children}
                              </code>
                            );
                          }
                        }}
                      >
                        {message.text}
                      </ReactMarkdown>
                    </div>
                    <div className="message-actions">
                      <button className="action-btn" title="Copy">
                        <Copy size={12} />
                      </button>
                      <button className="action-btn" title="Good response">
                        <ThumbsUp size={12} />
                      </button>
                      <button className="action-btn" title="Bad response">
                        <ThumbsDown size={12} />
                      </button>
                      <button className="action-btn" title="Share">
                        <Share size={12} />
                      </button>
                      <button className="action-btn" title="Regenerate">
                        <RotateCcw size={12} />
                      </button>
                      <button className="action-btn" title="More">
                        <MoreHorizontal size={12} />
                      </button>
                    </div>
                  </div>
                )}
              </motion.div>
            ))}
          </AnimatePresence>

          {isTyping && (
            <motion.div
              initial={{ opacity: 0, y: 20 }}
              animate={{ opacity: 1, y: 0 }}
              className="message ai typing"
            >
              <div className="typing-indicator">
                <span></span>
                <span></span>
                <span></span>
              </div>
            </motion.div>
          )}
          <div ref={messagesEndRef} />
        </div>

        {/* Search Input */}
        <motion.div 
          className="search-container"
          initial={{ opacity: 0, y: 30 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.8, delay: 0.6 }}
        >
          <div className="search-wrapper">
            <Search className="search-icon" size={20} />
            <textarea
              ref={textareaRef}
              value={inputText}
              onChange={handleInputChange}
              onKeyPress={handleKeyPress}
              placeholder="Ask anything"
              className="search-input"
              style={{ textAlign: 'left', resize: 'none' }}
              rows={1}
            />
            {inputText && (
              <motion.button
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                onClick={handleSendMessage}
                className="send-btn"
              >
                <Send size={18} />
              </motion.button>
            )}
          </div>
        </motion.div>
      </div>
    </div>
  );
};

export default NewChatInterface;
