import React from 'react';
import { useParams } from 'react-router-dom';
import { MapPin, Calendar, Heart, MessageCircle, Share, Coffee } from 'lucide-react';
import { motion } from 'framer-motion';
import './ProfilePage.css';

interface ProfileData {
  id: string;
  name: string;
  persona: string;
  location: string;
  joinDate: string;
  bio: string;
  avatar: string;
  coverImage: string;
  posts: Post[];
  interests: string[];
  stats: {
    connections: number;
    posts: number;
    likes: number;
  };
}

interface Post {
  id: string;
  type: 'thought' | 'moment' | 'ask' | 'offer';
  content: string;
  image?: string;
  timestamp: string;
  likes: number;
  comments: number;
  tags: string[];
}

const ProfilePage: React.FC = () => {
  const { userId } = useParams<{ userId: string }>();

  // Mock data - in a real app, this would come from an API
  const profile: ProfileData = {
    id: userId || 'me',
    name: 'Emma Chen',
    persona: 'Rory Gilmore',
    location: 'San Francisco, CA',
    joinDate: 'March 2024',
    bio: 'Coffee enthusiast ☕ | Book lover 📚 | Always planning spontaneous adventures ✈️ | Looking to connect with creative souls and fellow wanderers',
    avatar: 'https://images.unsplash.com/photo-1494790108755-2616b612b786?w=150&h=150&fit=crop&crop=face',
    coverImage: 'https://images.unsplash.com/photo-1506905925346-21bda4d32df4?w=800&h=300&fit=crop',
    posts: [
      {
        id: '1',
        type: 'ask',
        content: 'Anyone know good coffee shops in the Mission? Looking for a cozy spot to work on my novel this week! ☕📝',
        timestamp: '2 hours ago',
        likes: 12,
        comments: 8,
        tags: ['coffee', 'mission', 'writing', 'SF']
      },
      {
        id: '2',
        type: 'moment',
        content: 'Just finished reading "The Seven Husbands of Evelyn Hugo" and I am OBSESSED. Anyone else crying over this masterpiece? 😭✨',
        image: 'https://images.unsplash.com/photo-1481627834876-b7833e8f5570?w=400&h=300&fit=crop',
        timestamp: '1 day ago',
        likes: 28,
        comments: 15,
        tags: ['books', 'reading', 'bookclub']
      },
      {
        id: '3',
        type: 'offer',
        content: 'Hosting a book club meetup this Saturday! We\'re discussing contemporary fiction. DM me if you want to join our little literary circle 📚💕',
        timestamp: '3 days ago',
        likes: 19,
        comments: 12,
        tags: ['bookclub', 'meetup', 'SF', 'community']
      },
      {
        id: '4',
        type: 'thought',
        content: 'There\'s something magical about rainy afternoons in the city. Perfect excuse to curl up with a good book and some tea 🌧️📖',
        image: 'https://images.unsplash.com/photo-1502780402662-acc01917738e?w=400&h=300&fit=crop',
        timestamp: '5 days ago',
        likes: 34,
        comments: 7,
        tags: ['mood', 'rain', 'books', 'cozy']
      }
    ],
    interests: ['Literature', 'Coffee Culture', 'Travel', 'Photography', 'Indie Films', 'Sustainable Living'],
    stats: {
      connections: 247,
      posts: 89,
      likes: 1.2
    }
  };

  const getPostIcon = (type: string) => {
    switch (type) {
      case 'ask': return <MessageCircle size={16} />;
      case 'offer': return <Heart size={16} />;
      case 'moment': return <Calendar size={16} />;
      default: return <Coffee size={16} />;
    }
  };

  const getPostColor = (type: string) => {
    switch (type) {
      case 'ask': return '#3b82f6';
      case 'offer': return '#ef4444';
      case 'moment': return '#8b5cf6';
      default: return '#06b6d4';
    }
  };

  return (
    <div className="profile-container">
      {/* Cover Section */}
      <div className="cover-section">
        <div 
          className="cover-image"
          style={{ backgroundImage: `url(${profile.coverImage})` }}
        />
        <div className="cover-overlay" />
        <div className="profile-header">
          <div className="profile-avatar">
            <img src={profile.avatar} alt={profile.name} />
          </div>
          <div className="profile-info">
            <h1>{profile.name}</h1>
            <p className="persona">She's a girl written by {profile.persona} ✨</p>
            <div className="profile-meta">
              <span><MapPin size={16} /> {profile.location}</span>
              <span><Calendar size={16} /> Joined {profile.joinDate}</span>
            </div>
          </div>
          <div className="profile-actions">
            <button className="action-btn primary">
              <MessageCircle size={18} />
              Connect
            </button>
            <button className="action-btn secondary">
              <Share size={18} />
            </button>
          </div>
        </div>
      </div>

      {/* Stats & Bio */}
      <div className="profile-content">
        <div className="stats-section">
          <div className="stat">
            <span className="stat-number">{profile.stats.connections}</span>
            <span className="stat-label">Connections</span>
          </div>
          <div className="stat">
            <span className="stat-number">{profile.stats.posts}</span>
            <span className="stat-label">Posts</span>
          </div>
          <div className="stat">
            <span className="stat-number">{profile.stats.likes}k</span>
            <span className="stat-label">Likes</span>
          </div>
        </div>

        <div className="bio-section">
          <p>{profile.bio}</p>
        </div>

        <div className="interests-section">
          <h3>Interests</h3>
          <div className="interests-grid">
            {profile.interests.map((interest, index) => (
              <span key={index} className="interest-tag">
                {interest}
              </span>
            ))}
          </div>
        </div>

        {/* Posts Grid - Pinterest Style */}
        <div className="posts-section">
          <h3>Recent Posts</h3>
          <div className="posts-grid">
            {profile.posts.map((post, index) => (
              <motion.div
                key={post.id}
                initial={{ opacity: 0, y: 20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{ delay: index * 0.1 }}
                className="post-card"
              >
                <div className="post-header">
                  <div className="post-type" style={{ color: getPostColor(post.type) }}>
                    {getPostIcon(post.type)}
                    <span>{post.type}</span>
                  </div>
                  <span className="post-time">{post.timestamp}</span>
                </div>
                
                {post.image && (
                  <div className="post-image">
                    <img src={post.image} alt="Post" />
                  </div>
                )}
                
                <div className="post-content">
                  <p>{post.content}</p>
                </div>
                
                <div className="post-tags">
                  {post.tags.map((tag, tagIndex) => (
                    <span key={tagIndex} className="post-tag">
                      #{tag}
                    </span>
                  ))}
                </div>
                
                <div className="post-stats">
                  <div className="post-stat">
                    <Heart size={16} />
                    <span>{post.likes}</span>
                  </div>
                  <div className="post-stat">
                    <MessageCircle size={16} />
                    <span>{post.comments}</span>
                  </div>
                </div>
              </motion.div>
            ))}
          </div>
        </div>
      </div>


    </div>
  );
};

export default ProfilePage;
